@use "../../sass/base";

.chat-bubble {
    --bubble-border: var(--color-grey-dark-1);
    --bubble-background: var(--color-grey-light-1);
    --bubble-text: var(--color-grey-dark-1);
    
    position: absolute;
    width: fit-content;
    min-width: 200px;
    top: -6rem;
    left: 85%;
    transform: translateX(-50%);
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.5s ease;
    z-index: 100;

    &.visible {
        opacity: 1;
        pointer-events: auto;
    }

    .bub-part-a {
        margin-left: 4px;
        width: calc(100% - 8px);
        height: 1px;
        background: var(--bubble-border);
    }

    .bub-part-b {
        margin-left: 3px;
        width: calc(100% - 6px);
        height: 1px;
        background: var(--bubble-background);
        border-left: 1px solid var(--bubble-border);
        border-right: 1px solid var(--bubble-border);
    }

    .bub-part-c {
        margin-left: 2px;
        width: calc(100% - 4px);
        height: 1px;
        background: var(--bubble-background);
        border-left: 1px solid var(--bubble-border);
        border-right: 1px solid var(--bubble-border);
    }

    .speech-txt {
        width: calc(100% - 4px);
        margin-left: 2px;
        padding: 6px;
        background: var(--bubble-background);
        border-left: 1px solid var(--bubble-border);
        border-right: 1px solid var(--bubble-border);
        font-size: 1.1rem;
        line-height: 1.3;
        text-align: center;
        color: var(--bubble-text);

        .hint-text {
            display: block;
            padding: 0 3px;
        }

        .password-hint {
            display: block;
            height: 0;
            opacity: 0;
            padding: 0;
            margin: 0;
            font-style: italic;
            transition: all 0.3s ease;
        }
    }

    .speech-arrow {
        margin-top: -1px;
        margin-left: 50%;
        transform: translateX(-50%);

        .arrow-w {
            width: 6px;
            height: 6px;
            background: var(--bubble-background);
            border-left: 1px solid var(--bubble-border);
            border-right: 1px solid var(--bubble-border);
        }

        .arrow-x {
            margin-left: -1px;
            width: 4px;
            height: 2px;
            background: var(--bubble-background);
            border-left: 1px solid var(--bubble-border);
            border-right: 1px solid var(--bubble-border);
        }

        .arrow-y {
            margin-left: -1px;
            width: 4px;
            height: 2px;
            background: var(--bubble-background);
            border-left: 1px solid var(--bubble-border);
            border-right: 1px solid var(--bubble-border);
        }

        .arrow-z {
            margin-left: -1px;
            width: 6px;
            height: 1px;
            background: var(--bubble-border);
        }
    }

    &:hover {
        .speech-txt .password-hint {
            height: auto;
            opacity: 1;
            padding: 6px 3px 0;
            margin-top: 4px;
            border-top: 1px solid var(--bubble-border);
        }
    }
}

.header__image-container:hover .chat-bubble {
    /* Removed this block */
}

[data-theme='dark'] .chat-bubble {
    --bubble-border: var(--color-grey-light-1);
    --bubble-background: var(--color-grey-dark-2);
    --bubble-text: var(--color-grey-light-1);
}