@use 'sass:math';
@use '../../sass/base' as *;

.tools {
  padding: 4rem 0;
  position: relative;
  background: var(--color-background);
  min-height: 100vh;

  h1 {
    margin-bottom: 3rem;
    text-align: center;
    color: var(--text-color);
  }

  .container__content {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__tabs {
    display: inline-flex;
    background: var(--color-grey-dark-2);
    padding: 4px;
    border-radius: 50px;
    position: relative;
    box-shadow: var(--shadow-dark);
    margin-bottom: 2rem;
    min-width: 400px;
    justify-content: space-between;

    button {
      color: var(--text-color);
      padding: 12px 24px;
      flex: 1;
      position: relative;
      z-index: 2;
      transition: all 0.3s var(--bezier-curve);
      cursor: pointer;
      text-transform: uppercase;
      font-size: 0.9rem;
      font-weight: 500;
      background: none;
      border: none;
      outline: none;
      text-align: center;
      white-space: nowrap;

      i {
        margin-right: 8px;
        font-size: 1rem;
      }

      &.active {
        color: white;
        font-weight: 600;
        text-shadow: 0 1px 4px rgba(0, 0, 0, 0.3);
        transform: translateY(-1px);
      }

      &:hover:not(.active) {
        color: var(--color-primary-light);
      }

      &:focus {
        outline: none;
      }

      &:focus-visible {
        outline: 2px solid var(--color-primary);
        outline-offset: 2px;
        border-radius: 25px;
      }
    }

    .selector {
      height: calc(100% - 8px);
      width: calc(33.333333% - 4px);
      display: block;
      position: absolute;
      top: 4px;
      left: 4px;
      z-index: 1;
      border-radius: 25px;
      transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
      background: linear-gradient(
        135deg,
        var(--color-primary) 0%,
        var(--color-primary-light) 100%
      );
      box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
      opacity: 0.95;
    }
  }

  &__content {
    width: 100%;
    background: var(--color-grey-dark-2);
    border-radius: 12px;
    padding: 2.5rem;
    box-shadow: var(--shadow-dark);
    min-height: 400px;
  }

  &__loading {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 200px;
    gap: 1rem;
    color: var(--text-color);

    i {
      font-size: 2rem;
      animation: spin 1s infinite linear;
    }
  }
}

.tool-selector {
  background: var(--color-background-light);
  border-radius: var(--border-radius);
  padding: 0.5rem;
  box-shadow: var(--shadow-medium);
  margin-bottom: 2rem;

  &__tools {
    display: flex;
    position: relative;
    background: var(--color-background);
    border-radius: var(--border-radius);
    padding: 0.25rem;
  }

  &__option {
    flex: 1;
    position: relative;
    z-index: 1;
    cursor: pointer;
    transition: color 0.3s ease;

    &-content {
      padding: 0.75rem 1.5rem;
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 0.75rem;
      
      i {
        font-size: 1.2rem;
      }

      span {
        font-weight: 500;
      }
    }

    &.selected {
      color: white;
    }
  }

  &__slider {
    position: absolute;
    top: 0.25rem;
    left: 0.25rem;
    width: calc(50% - 0.25rem);
    height: calc(100% - 0.5rem);
    background: var(--color-primary);
    border-radius: calc(var(--border-radius) - 0.25rem);
    transition: transform 0.3s ease;
  }
}

.tool-card {
  &__header {
    display: flex;
    align-items: center;
    gap: 1rem;
    margin-bottom: 1.5rem;

    i {
      font-size: 2rem;
      color: var(--color-primary);
    }

    h3 {
      font-size: 1.5rem;
      margin: 0;
      color: var(--color-text);
    }
  }

  &__tags {
    margin-left: auto;
    display: flex;
    gap: 0.5rem;
  }

  &__tag {
    padding: 0.25rem 0.75rem;
    border-radius: 1rem;
    font-size: 0.85rem;
    font-weight: 500;
    color: white;
  }

  &__description {
    color: var(--color-text-light);
    font-size: 1.1rem;
    line-height: 1.6;
    margin-bottom: 1.5rem;
  }

  &__features {
    display: flex;
    flex-wrap: wrap;
    gap: 0.75rem;
  }

  &__feature {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    font-size: 0.9rem;
    color: var(--color-text-light);
    background: var(--color-background);
    padding: 0.35rem 0.75rem;
    border-radius: 0.5rem;
    
    i {
      font-size: 0.9rem;
      color: var(--color-primary);
    }
  }
}

@media only screen and (max-width: $bp-large) {
  .tools {
    &__tabs {
      button {
        padding: 8px 16px;
        font-size: 0.85rem;
      }
    }
  }

  .tool-card {
    &__header {
      flex-wrap: wrap;

      h3 {
        flex: 1;
      }
    }

    &__tags {
      width: 100%;
      margin: 1rem 0 0 0;
    }
  }
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
